import React, {Component} from 'react';

require('./progress.less');

export default class Progress extends Component {

    changeProgress(e) {
        let progressBar = this.refs.progressBar;
        let progress = (e.clientX - progressBar.getBoundingClientRect().left)/progressBar.clientWidth;
        // this.setState({progress: progress});   这样做行不通，setState只能在初始化了state的那个组件中使用，不能在其它组件中使用
        // console.log(progress);
        this.props.onProgressChange && this.props.onProgressChange(progress);
    }

    render() {
        return (
            <div className="components-progress" onClick={this.changeProgress.bind(this)}
                 ref="progressBar">
                <div className="progress"
                     style={{width: `${this.props.progress}%`, background: this.props.barColor}}>
                </div>
            </div>
        )
    }
}


